@use '../scss/variables' as *;
@use '../scss/mixins';

.bid {
	@include mixins.flex(row);
	align-items: center;
	gap: 1.5rem;

	&__image {
		width: 7rem;
		height: 7rem;
	}

	&__description {
		display: grid;
		grid-template-areas: 'title title' 'amount status';
		grid-template-columns: min-content 1fr;
		flex-grow: 1;
	}

	&__title {
		grid-area: title;
		font-size: 1.5rem;
		font-weight: 500;
		max-width: 70%;
	}

	&__amount {
		@include mixins.flex(row);
		grid-area: amount;
		font-weight: 500;
		text-wrap: nowrap;
		&::after {
			content: ' ₽';
		}
	}

	&__status {
		grid-area: status;
		padding-left: 1rem;
		color: $statusOpened;
		&::before {
			content: '•';
			margin-right: 0.5rem;
		}
	}

	&__open {
		min-width: 36px;
		width: 36px;
		height: 36px;
		background-image: url(../images/shevron-right.svg);
		border: 0;
		background-size: cover;
		background-color: transparent;
		cursor: pointer;

		&:hover {
			opacity: 0.6;
		}
	}

	&__selector {
		position: relative;
		min-width: 2rem;
		width: 2rem;
		height: 2rem;
		border: 0;

		> input {
			opacity: 0;
		}

		&:has(input:checked) {
			&::after {
				background-image: url(../images/checkbox-filled.svg);
			}
		}

		&::after {
			content: ' ';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-image: url(../images/checkbox-empty.svg);
		}
	}
}
