@use '../scss/variables' as *;
@use '../scss/mixins';

.footer {
	background-color: $secondaryBackground;

	&__container {
		@include mixins.container(1rem, 1rem, 0);
		@include mixins.flex(row);
		justify-content: space-between;
		align-items: center;
	}

	&__logo {
		height: $headerElementsSize;

		&-image {
			display: block;
			height: 100%;
		}
	}

	&__menu {
		display: flex;
		gap: 2.5rem;
		font-size: 1rem;
		font-weight: 400;
	}

	&__menu-item {
		color: $secondary;
		text-decoration: none;
		&:hover {
			color: $hover;
		}
	}
}
