@use '../scss/variables' as *;
@use '../scss/mixins';

.about {
	@include mixins.container(
		$defaultPadding,
		$extendedPadding,
		$extendedPadding
	);
	@include mixins.flex(column);
	justify-content: flex-start;
	align-items: center;
	position: relative;
	gap: 1rem;
	background-color: white;
	border-radius: $borderRadius;
	box-sizing: border-box;

	&__image {
		object-fit: cover;
	}
	&__title {
		font-size: 2.5rem;
		margin: 0;
		z-index: 1;
	}
	&__description {
		font-size: 1.5rem;
		margin: 0;
		z-index: 1;
	}
}
